.btn {
  background-color: #333;
  color: #fff;
  border: 1px solid #fff;
  display: block;
  font-size: 1.25rem;
  font-weight: 400;
  line-height: 1.45;
  letter-spacing: 0.125rem;
  text-align: center;
  cursor: pointer;
  max-width: 20.9375rem;
  width: 100%;
  margin: 0 auto;
}
@media screen and (min-width: 768px) {
