    font-size: 1rem;
    max-width: 22.5rem;
    margin: initial;
  }
}

.btn span {
  position: relative;
  z-index: 3;
  /*z-indexの数値をあげて文字を背景よりも手前に表示*/
  color: #fff;
}

.btn:hover span {
  color: #333;
}

.btn a {
  display: inline-block;
  width: inherit;
  padding: 0.5625rem 0;
}
@media screen and (min-width: 768px) {
  .btn a {
    padding: 0 0.3125rem;
    position: relative;
    line-height: 3.375;
  }
}

.btn a::before {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1;
  content: "";
  background: #fff;
  transform-origin: right top;
  transform: scale(0, 1);
  transition: transform 0.3s;
}

@media screen and (min-width: 768px) {
  .btn a:hover::before {
    transform-origin: left top;
    transform: scale(1, 1);
  }
}

