.arrow::before {
  content: "";
  position: absolute;
  width: 1.25rem;
  height: 1.25rem;
  border-top: solid 3px #333;
  border-right: solid 3px #333;
  transform: translate(-50%, -50%) rotate(-45deg);
  position: absolute;
  left: 50%;
  top: 60%;
}

.arrow:hover:before {
  border-top: solid 3px #fff;
